import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { Row, Col, Typography, Button, Spin, Image, Divider, Result } from 'antd'
import QRCode from 'react-qr-code'
import $ from 'jquery'
import { saveAs } from 'file-saver'
import { getDatosUsuario, getToken, getTypeContent } from '../../utils/Utils'
import * as apiURLs from '../../utils/urls'
import toast from 'react-hot-toast'

import PostulantService from '../../service/PostulantService'
import CertificateService from '../../service/CertificateService'

const postulantService = new PostulantService()

const Certificate = () => {

    const navigate = useHistory()
    const [postulant, setPostulant] = useState({})
    const [load, setLoad] = useState(true)
    const [isLoading, setIsLoading] = useState(false)
    const [isDownloading, setIsDownloading] = useState(false)

    const downloadCertificate = async (cerNombre, cerRuta) => {
        const metaData = getTypeContent(cerNombre)

        var xhrOverride = new XMLHttpRequest();
        xhrOverride.responseType = 'arraybuffer';

        setIsDownloading(true)
        $.ajax({
            async: true,
            crossDomain: true,
            url: apiURLs.downloadCertificateByName(),
            method: 'POST',
            headers: {
                Accept: "*/*",
                Authorization: `Bearer ${getToken()}`,
                "Content-Type": "application/json"
            },
            data: JSON.stringify({
                cerNombre,
                cerRuta
            }),
            xhr: function () {
                return xhrOverride;
            }
        }).then(function (response) {
            setIsDownloading(false)
            const blob = new Blob([response], { type: metaData })
            saveAs(blob, cerNombre)
        }).catch(() => setIsDownloading(false));
    }

    const renderCertificate = ({ cerEstado, cerFechaCad, cerFechaReg, cerId, cerNombre, cerRuta, idCurso }) =>
        <div
            key={cerId}
            style={
                {
                    backgroundColor: '#ffffff',
                    padding: '15px',
                    borderRadius: '15px',
                    marginBottom: '20px'
                }
            }>
            <Row>
                <Col xs={24} sm={24} md={16} lg={16} xl={16} xxl={12}>
                    <Row gutter={10}>
                        <Col>
                            <Image src={`./../../assets/${cerEstado.catValor === 'EST_CER_ACT' ? 'accept.png' : 'cancel.png'}`} preview={false} width={24} height={24} />
                        </Col>
                        <Col>
                            <Typography.Text strong style={{ fontSize: '1.1em' }}>
                                Certificate&nbsp;-&nbsp;
                                {
                                    postulant && postulant.entId.cursoList.filter(c => c.curId === idCurso)[0].curNombre.catNombre
                                }
                            </Typography.Text>
                        </Col>
                    </Row>
                </Col>
                <Col xs={24} sm={12} md={4} lg={4} xl={4} xxl={6}>
                    <Row gutter={10}>
                        <Col>
                            <Typography.Text strong style={{ fontSize: '1.1em' }}>
                                Expires: {cerFechaCad.substring(0, 10)}
                            </Typography.Text>
                        </Col>
                    </Row>
                </Col>
                <Col xs={24} sm={12} md={4} lg={4} xl={4} xxl={6}>
                    <Row gutter={10} justify='end'>
                        <Col>
                            <Button type='primary'
                                loading={isDownloading}
                                disabled={isDownloading}
                                onClick={() => downloadCertificate(cerNombre, cerRuta)}
                            >Download</Button>
                        </Col>
                    </Row>
                </Col>
            </Row>
        </div>

    const fetchData = async () => {
        setLoad(false)
        setIsLoading(true)
        try {
            const postulant = { idUsuario: getDatosUsuario().usuarioId }
            const { data } = await postulantService.getTraining({ navigate, postulant })
            const training = data.listObject[0]
            setPostulant(training)
        } catch (error) {
            const { response } = error
            toast.error(response.status === 409 ? response.data[0].messageText : 'An error occurred')
        } finally {
            setIsLoading(false)
        }
    }

    useEffect(() => {
        if (load) fetchData()
    })

    return (
        <>
            {
                isLoading ?
                    <Row style={{ padding: '30px 10px' }}>
                        <Col span={24} style={{ textAlign: 'center' }}>
                            <Spin size='large' tip={
                                <Typography.Paragraph style={{ padding: '10px 0' }}>
                                    Loading...
                                </Typography.Paragraph>
                            } />
                        </Col>
                    </Row>
                    :
                    postulant && postulant.informeList && postulant.certificadoList &&
                    <>
                        {
                            postulant.informeList.filter(w => w.infTipo.catValor === 'INFORME_FINAL' &&
                                w.infEstado.catValor === 'EST_INF_APR'
                            ).length > 0
                                &&
                                postulant.certificadoList.length >= 0
                                ?
                                <>
                                    {
                                        postulant.certificadoList && postulant.certificadoList.map(c => renderCertificate(c))
                                    }
                                </>
                                :
                                <>
                                    <Result
                                        title="You will see your certificate once your final report is approved"
                                        extra={
                                            <Button type="primary" key="console"
                                                onClick={() => {
                                                    navigate.push('/finalreport')
                                                }}
                                            >
                                                Send Final Report
                                            </Button>
                                        }
                                    />
                                </>
                        }
                    </>
            }
        </>
    )
}

export default Certificate