import React, { useEffect, useState } from 'react'
import { useHistory, useParams } from 'react-router-dom'
import { Tabs, Button, Row, Col, Image, Form, Input, Typography, Space, Spin, InputNumber, Popconfirm, Tooltip, Descriptions, message, Collapse } from 'antd'
import * as apiURLs from '../../utils/urls'
import { getCatalogos, getToken, getTypeContent } from '../../utils/Utils'
import $ from 'jquery'
import { saveAs } from 'file-saver'

import TrainingService from '../../service/TrainingService'
import AttendanceService from '../../service/AttendanceService'
import InformService from '../../service/InformService'
import CertificateService from '../../service/CertificateService'

import toast from 'react-hot-toast'

const trainingService = new TrainingService()
const attendanceService = new AttendanceService()
const informService = new InformService()
const certificateService = new CertificateService()

const panelStyle = {
  marginBottom: 20,
  backgroundColor: '#fafafa',
  border: 'none',
  borderRadius: '15px'
}

const Applicant = () => {

  const navigate = useHistory()
  const { id, idTraining } = useParams()
  const [load, setLoad] = useState(true)
  const [isLoading, setIsLoading] = useState(false)
  const [postulant, setPostulante] = useState(null)
  const [courses, setCourses] = useState(null)
  const [attendanceValue, setAttendanceValue] = useState(0)
  const [commentWorkplan, setCommentWorkplan] = useState('')
  const [commentFinalReport, setCommentFinalReport] = useState('')
  const [isSavingAttendance, setIsSavingAttendance] = useState(false)
  const [isSavingInform, setIsSavingInform] = useState(false)
  const [isSending, setIsSending] = useState(false)
  const [isGenerating, setIsGenerating] = useState(false)
  const [isSendingCertificate, setIsSendingCertificate] = useState(false)
  const [isDownloading, setIsDownloading] = useState(false)

  const renderInform = ({ infId, infFechaReg, infFechaSAprRec, infNombre, infEstado, infObservacion, infRutaPlan, evidenciaList, infTipo }) =>
    <Row
      key={infId}
      className='mgeCard'
      style={{
        backgroundColor: '#f0f0f0',
        padding: '10px 20px',
        marginBottom: '10px',
      }}>
      <Descriptions
        title={
          <Row gutter={10} style={{ paddingBottom: '10px' }}>
            <Col>
              <Image src={`./../../assets/${infEstado.catValor === 'EST_INF_ENV' ? 'send.png' : infEstado.catValor === 'EST_INF_APR' ? 'accept.png' : infEstado.catValor === 'EST_INF_REC' ? 'cancel.png' : 'minus.png'}`} preview={false} width={24} height={24} />
            </Col>
            <Col>
              <Typography.Text
                strong
                style={{
                  fontWeight: 'normal',
                }}
              >
                {infNombre}
              </Typography.Text>
            </Col>
          </Row>
        }
        size='small'
        layout='vertical'
        extra={
          <>
            <Button type='default'
              onClick={() => downloadInform({ infNombre, infRutaPlan })}
              loading={isDownloading}
              disabled={isDownloading}
            >
              Download
            </Button>
          </>
        }
        column={
          {
            xs: 2,
            sm: 2,
            md: 4,
            lg: 4,
            xl: 6,
            xxl: 6,
          }
        }
      >

        <Descriptions.Item label={
          <Typography.Text type='secondary' strong>Registration date</Typography.Text>
        }
        >
          {infFechaReg.substring(0, 10)}
        </Descriptions.Item>

        {
          evidenciaList && evidenciaList.length > 0 ?
            <>
              <Descriptions.Item label={
                <Typography.Text type='secondary' strong>Evidences</Typography.Text>
              }
              >
                <a rel="noreferred noreferrer" target='_blank' href={`${evidenciaList[0].eviUrl}`}>
                  {
                    evidenciaList[0].eviUrl.length > 15 ?
                      `${evidenciaList[0].eviUrl.substring(0, 15)}...`
                      : evidenciaList[0].eviUrl
                  }
                </a>
              </Descriptions.Item>
            </>
            :
            <>
            </>
        }

        {
          infFechaSAprRec &&
          <Descriptions.Item label={
            <Typography.Text type='secondary' strong>Review date</Typography.Text>
          }
          >
            {infFechaSAprRec.substring(0, 10)}
          </Descriptions.Item>
        }

        {
          infObservacion &&
          <Descriptions.Item label={
            <Typography.Text type='secondary' strong>Observation</Typography.Text>
          }
          >
            {infObservacion}
          </Descriptions.Item>
        }

      </Descriptions>

      {
        infEstado.catValor === 'EST_INF_ENV' &&
        <>
          <Col span={24} style={{ padding: '10px 0' }}>
            <Typography.Text>
              Comments:
            </Typography.Text>
            {
              infTipo.catValor === 'PLAN_TRABAJO' ?
                <Input.TextArea
                  disabled={
                    isSavingInform
                  }
                  value={commentWorkplan}
                  onChange={(e) => setCommentWorkplan(e.target.value)}
                />
                :
                <Input.TextArea
                  disabled={
                    isSavingInform
                  }
                  value={commentFinalReport}
                  onChange={(e) => setCommentFinalReport(e.target.value)}
                />
            }
          </Col>
          <Col span={24}>
            <Row justify='end'
              style={{ padding: '10px 0 0 0' }}
              gutter={10}
            >
              <Col>
                <Popconfirm
                  title={
                    `Are you sure to approve the ${infTipo.catValor === 'PLAN_TRABAJO' ? 'workplan' : infTipo.catValor === 'INFORME_FINAL' ? 'final report' : ''}`
                  }
                  okText="Yes, approve" cancelText="No" onConfirm={
                    () =>
                      setInform(
                        postulant.informeList.filter(i => i.infTipo.catValor === infTipo.catValor && i.infEstado.catValor === 'EST_INF_ENV')[0]
                        , getCatalogos().reportStatus.filter(s => s.catValor === 'EST_INF_APR')[0]
                      )
                  }
                  okButtonProps={{
                    loading: isSavingInform || isSending || isGenerating || isSendingCertificate
                  }}
                  disabled={isSavingInform || isSending || isGenerating || isSendingCertificate}
                >
                  <Button
                    type='primary'
                    loading={isSavingInform || isSending || isGenerating || isSendingCertificate}
                    disabled={isSavingInform || isSending || isGenerating || isSendingCertificate}
                  >Approve</Button>
                </Popconfirm>
              </Col>
              <Col>
                <Popconfirm
                  title={
                    `Are you sure to approve the ${infTipo.catValor === 'PLAN_TRABAJO' ? 'workplan' : infTipo.catValor === 'INFORME_FINAL' ? 'final report' : ''}`
                  }
                  okText="Yes, reject" cancelText="No" onConfirm={
                    () =>
                      setInform(
                        postulant.informeList.filter(i => i.infTipo.catValor === infTipo.catValor && i.infEstado.catValor === 'EST_INF_ENV')[0]
                        , getCatalogos().reportStatus.filter(s => s.catValor === 'EST_INF_REC')[0]
                      )
                  }
                  okButtonProps={{ loading: isSavingInform || isSending || isGenerating || isSendingCertificate }}
                  disabled={isSavingInform || isSending || isGenerating || isSendingCertificate}
                >
                  <Button
                    danger
                    type='primary'
                    loading={isSavingInform || isSending || isGenerating || isSendingCertificate}
                    disabled={isSavingInform || isSending || isGenerating || isSendingCertificate}
                  >Reject</Button>
                </Popconfirm>
              </Col>
            </Row>
          </Col>
        </>
      }

    </Row>

  const renderCourse = ({ curId, curNombre, infTipo }) =>
    <Collapse.Panel
      key={curId}
      header={
        <Row gutter={10}>
          <Col>
            {
              postulant.informeList && postulant.informeList.filter(i => i.infTipo.catValor === infTipo.catValor && i.idCurso === curId).length > 0 ?
                <Image src={`./../../assets/${postulant.informeList.filter(i => i.infTipo.catValor === infTipo.catValor && i.idCurso === curId)[postulant.informeList.filter(i => i.infTipo.catValor === infTipo.catValor && i.idCurso === curId).length - 1].infEstado.catValor === 'EST_INF_ENV' ? 'send.png' : postulant.informeList.filter(i => i.infTipo.catValor === infTipo.catValor && i.idCurso === curId)[postulant.informeList.filter(i => i.infTipo.catValor === infTipo.catValor && i.idCurso === curId).length - 1].infEstado.catValor === 'EST_INF_APR' ? 'accept.png' : postulant.informeList.filter(i => i.infTipo.catValor === infTipo.catValor && i.idCurso === curId)[postulant.informeList.filter(i => i.infTipo.catValor === infTipo.catValor && i.idCurso === curId).length - 1].infEstado.catValor === 'EST_INF_REC' ? 'cancel.png' : 'minus.png'}`} preview={false} width={24} height={24} />
                :
                <Image src='./../../assets/minus.png' preview={false} width={24} height={24} />
            }
          </Col>
          <Col>
            <Typography.Text strong style={{ fontSize: '1.1em' }}>
              {
                infTipo.catValor === 'PLAN_TRABAJO' ? 'Workplan - ' : infTipo.catValor === 'INFORME_FINAL' ? 'Final Report - ' : ''
              }
              {curNombre.catNombre}
            </Typography.Text>
          </Col>
        </Row>
      }
      style={panelStyle}
    >
      {
        postulant.informeList && postulant.informeList.filter(i => i.infTipo.catValor === infTipo.catValor && i.idCurso === curId).map(i => renderInform(i))
      }
    </Collapse.Panel>

  const downloadCertificate = async (cerNombre, cerRuta) => {
    const metaData = getTypeContent(cerNombre)

    var xhrOverride = new XMLHttpRequest();
    xhrOverride.responseType = 'arraybuffer';

    setIsDownloading(true)
    $.ajax({
      async: true,
      crossDomain: true,
      url: apiURLs.downloadCertificateByName(),
      method: 'POST',
      headers: {
        Accept: "*/*",
        Authorization: `Bearer ${getToken()}`,
        "Content-Type": "application/json"
      },
      data: JSON.stringify({
        cerNombre,
        cerRuta
      }),
      xhr: function () {
        return xhrOverride;
      }
    }).then(function (response) {
      setIsDownloading(false)
      const blob = new Blob([response], { type: metaData })
      saveAs(blob, cerNombre)
    }).catch(() => setIsDownloading(false));
  }

  const renderCertificate = ({ cerEstado, cerFechaCad, cerFechaReg, cerId, cerNombre, cerRuta, idCurso }) =>
    <div
      key={cerId}
      style={
        {
          backgroundColor: '#ffffff',
          padding: '15px',
          borderRadius: '15px',
          marginBottom: '20px'
        }
      }>
      <Row>
        <Col xs={24} sm={24} md={16} lg={16} xl={16} xxl={12}>
          <Row gutter={10}>
            <Col>
              <Image src={`./../../assets/${cerEstado.catValor === 'EST_CER_ACT' ? 'accept.png' : 'cancel.png'}`} preview={false} width={24} height={24} />
            </Col>
            <Col>
              <Typography.Text strong style={{ fontSize: '1.1em' }}>
                Certificate&nbsp;-&nbsp;
                {
                  courses && courses.filter(c => c.curId === idCurso)[0].curNombre.catNombre
                }
              </Typography.Text>
            </Col>
          </Row>
        </Col>
        <Col xs={24} sm={12} md={4} lg={4} xl={4} xxl={6}>
          <Row gutter={10}>
            <Col>
              <Typography.Text strong style={{ fontSize: '1.1em' }}>
                Expires: {cerFechaCad.substring(0, 10)}
              </Typography.Text>
            </Col>
          </Row>
        </Col>
        <Col xs={24} sm={12} md={4} lg={4} xl={4} xxl={6}>
          <Row gutter={10} justify='end'>
            <Col>
              <Button type='primary'
                loading={isDownloading}
                disabled={isDownloading}
                onClick={() => downloadCertificate(cerNombre, cerRuta)}
              >Download</Button>
            </Col>
          </Row>
        </Col>
      </Row>
    </div>

  const setAttendance = async () => {
    const attendance = {
      "idEntrenamiento": idTraining,
      "idPostulante": id,
      "asiAprobado": (parseInt(attendanceValue) >= 80),
      "asiValor": attendanceValue
    }
    setIsSavingAttendance(true)
    try {
      const { data } = await attendanceService.put({ navigate, attendance })
      toast.success('Attendance successfully saved')
    } catch (error) {
      const { response } = error
      toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
    } finally {
      setIsSavingAttendance(false)
    }
  }

  const downloadInform = ({ infNombre, infRutaPlan }) => {

    const metaData = getTypeContent(infNombre)

    var xhrOverride = new XMLHttpRequest();
    xhrOverride.responseType = 'arraybuffer';

    setIsDownloading(true)
    $.ajax({
      async: true,
      crossDomain: true,
      url: apiURLs.downloadInformByName(),
      method: 'POST',
      headers: {
        Accept: "*/*",
        Authorization: `Bearer ${getToken()}`,
        "Content-Type": "application/json"
      },
      data: JSON.stringify({
        infNombre,
        infRutaPlan
      }),
      xhr: function () {
        return xhrOverride;
      }
    }).then(function (response) {
      setIsDownloading(false)
      const blob = new Blob([response], { type: metaData })
      saveAs(blob, infNombre)
    }).catch(() => setIsDownloading(false));
  }

  const sendCertificate = async ({ cerId }) => {
    setIsSendingCertificate(true)
    try {
      const certificate = { cerId }
      const { data } = await certificateService.send({ navigate, certificate })
      toast.success('Certificate sent successfully')
      setLoad(true)
    } catch (_) {
      toast.error('Email send failed')
    } finally {
      setIsSendingCertificate(false)
    }
  }

  const generate = async ({ idCurso }) => {
    setIsGenerating(true)
    try {
      const course = {
        idCurso: idCurso,
        idPostulante: postulant.posId
      }
      const { data } = await certificateService.generate({ navigate, course })
      const certificate = data.listObject[0]
      toast.success('Certificate generated successfully')
      sendCertificate(certificate)
    } catch (error) {
      const { response } = error
      toast.error(response.status === 409 ? response.data[0].messageText : 'An error occurred')
    } finally {
      setIsGenerating(false)
    }
  }

  const sendRateEmail = async ({ infId, infTipo, infEstado, idCurso }) => {
    setIsSending(true)
    try {
      const inform = { infId }
      const { data } = await informService.sendEmailRate({ navigate, inform })
      toast.success('Email sent successfully')
      if (infTipo.catValor === 'INFORME_FINAL' && infEstado.catValor === 'EST_INF_APR') {
        generate({ idCurso })
      } else {
        setLoad(true)
      }
    } catch (_) {
      toast.error('Email send failed')
    } finally {
      setIsSending(false)
    }
  }

  const setInform = async ({ infId, infTipo, idCurso }, { catId }) => {
    const { catValor } = infTipo
    const inform = {
      infId,
      idEstado: catId,
      infObservacion: catValor === 'PLAN_TRABAJO' ? commentWorkplan : catValor === 'INFORME_FINAL' ? commentFinalReport : ''
    }
    setIsSavingInform(true)
    try {
      const { data } = await informService.set({ navigate, inform })
      const informe = data.listObject[0]
      sendRateEmail({ ...informe, idCurso })
    } catch (error) {
      const { response } = error
      toast.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
    } finally {
      setIsSavingInform(false)
    }
  }

  const fetchData = async () => {
    setLoad(false)
    setIsLoading(true)
    const training = {
      entId: idTraining
    }
    try {
      const { data } = await trainingService.getById({ navigate, training })
      if (data) {
        const courses = data.listObject[0].cursoList
        const postulant = data.listObject[0].postulanteList.filter(p => p.posId === parseInt(id))[0]
        if (postulant.asistenciaList.length > 0) {
          const attendanceValue = postulant.asistenciaList[postulant.asistenciaList.length - 1].asiValor || 0
          setAttendanceValue(attendanceValue)
        }
        setPostulante(postulant)
        setCourses(courses)
      } else {
        toast.error('An error occurred')
      }
    } catch (error) {
      const { response } = error
      message.error(response.status === 409 ? response.data[0].messageText : "An error occurred")
    } finally {
      setIsLoading(false)
    }
  }

  useEffect(() => {
    if (load)
      fetchData()
  })

  return (
    <>
      {
        isLoading ?
          <Row style={{ padding: '30px 10px' }}>
            <Col span={24} style={{ textAlign: 'center' }}>
              <Spin size='large' tip={
                <Typography.Paragraph style={{ padding: '10px 0' }}>
                  Loading...
                </Typography.Paragraph>
              } />
            </Col>
          </Row>
          :
          postulant && <>
            <div className="grid">
              <div className="col-12">
                <div className="mgeCard">
                  <div className="grid">
                    <div className="col-8">
                      <Space direction='horizontal'>
                        <Button icon={<i className="fa-solid fa-chevron-left"></i>}
                          onClick={() => {
                            navigate.push(`/training/${idTraining}`)
                          }}
                        ></Button>
                        <h3 style={{ padding: '0', margin: '0' }} className='mge-darkGreen'>
                          {
                            `${postulant.usuId.nombres || 'Postulante'} ${postulant.usuId.apellidos || ''}`
                          }
                        </h3>
                      </Space>
                    </div>
                    <div className="col-4" style={{ textAlign: 'right' }}>
                      {/* <Space>
                        <Button type='primary'>Edit</Button>
                      </Space> */}
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <Tabs centered items={[
              {
                key: 1,
                label: <Typography.Text style={{ fontSize: '1.1em', letterSpacing: '1px' }}>Information</Typography.Text>,
                children: <>
                  <Row gutter={[25, 25]} justify='center'>
                    <Col xs={22} sm={20} md={6} lg={6} xl={6} xxl={4}>
                      <Row gutter={[15, 15]} style={{ textAlign: 'center' }}>
                        <Col span={24}>
                          <Image preview src='./assets/user.png' />
                        </Col>
                        <Col xs={12} sm={12} md={24} lg={24} xl={24} xxl={24} style={{ textAlign: 'left' }}>
                          <Typography.Paragraph>
                            <Typography.Text strong>
                              Role:
                            </Typography.Text>
                            <br />Applicant
                          </Typography.Paragraph>
                        </Col>
                        <Col xs={12} sm={12} md={24} lg={24} xl={24} xxl={24} style={{ textAlign: 'left' }}>
                          <Typography.Paragraph>
                            <Typography.Text strong>
                              Invited by:
                            </Typography.Text>
                            <br />
                            {
                              postulant.posInvitadoPor ?
                                `${postulant.posInvitadoPor.nombres || ''} ${postulant.posInvitadoPor.apellidos || ''}`
                                : <Typography.Text type='danger'> Agregar este campo a servicio</Typography.Text>
                            }
                          </Typography.Paragraph>
                        </Col>
                      </Row>
                    </Col>
                    <Col xs={22} sm={20} md={14} lg={14} xl={14} xxl={10}>
                      <Form
                        initialValues={
                          { ...postulant.usuId, fechanac: postulant.usuId.fechanac.substring(0, 10) }
                        }
                        layout='vertical'
                      >
                        <Row gutter={[25, 5]}>
                          <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                            <Form.Item
                              name='nombres'
                              label="First Name"
                            >
                              <Input readOnly />
                            </Form.Item>
                          </Col>
                          <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                            <Form.Item
                              name='apellidos'
                              label="Last Name"
                            >
                              <Input readOnly />
                            </Form.Item>
                          </Col>
                          <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                            <Form.Item
                              name={['sexo', 'catNombre']}
                              label="Sex"
                            >
                              <Input readOnly />
                            </Form.Item>
                          </Col>
                          <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                            <Form.Item
                              name='fechanac'
                              label="Date Birth"
                            >
                              {/* <DatePicker style={{ width: '100%' }} /> */}
                              <Input readOnly />
                            </Form.Item>
                          </Col>
                          <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                            <Form.Item
                              name={['pais', 'catNombre']}
                              label="Country"
                            >
                              <Input readOnly />
                            </Form.Item>
                          </Col>
                          <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                            <Form.Item
                              name={['ciudad']}
                              label="City"
                            >
                              <Input readOnly />
                            </Form.Item>
                          </Col>
                          <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                            <Form.Item
                              name='codigotel'
                              label="Country Code"
                            >
                              <Input readOnly />
                            </Form.Item>
                          </Col>
                          <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                            <Form.Item
                              name='celular'
                              label="Phone number"
                            >
                              <Input readOnly />
                            </Form.Item>
                          </Col>
                          <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                            <Form.Item
                              name='documento'
                              label="ID Number"
                            >
                              <Input readOnly />
                            </Form.Item>
                          </Col>
                          <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                            <Form.Item
                              name='lugartrabajo'
                              label="Currenty Working"
                            >
                              <Input readOnly />
                            </Form.Item>
                          </Col>
                          <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                            <Form.Item
                              name='email'
                              label="Email"
                            >
                              <Input readOnly />
                            </Form.Item>
                          </Col>
                          <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                            <Form.Item
                              name='email2'
                              label="Additional Email"
                            >
                              <Input readOnly />
                            </Form.Item>
                          </Col>
                        </Row>
                      </Form>
                    </Col>
                  </Row>
                </>
              }, {
                key: 2,
                label: <Typography.Text style={{ fontSize: '1.1em', letterSpacing: '1px' }}>Progress</Typography.Text>,
                children: <>
                  <div style={
                    {
                      backgroundColor: '#ffffff',
                      padding: '15px',
                      borderRadius: '15px',
                      marginBottom: '20px'
                    }
                  }>
                    <Row>
                      <Col xs={12} sm={12} md={12} lg={12} xl={12} xxl={12}>
                        <Row gutter={10}>
                          <Col>
                            <img alt='' src={`./../../assets/${attendanceValue >= 80 ? 'accept.png' : 'cancel.png'}`} height={24} width={24} />
                          </Col>
                          <Col>
                            <Typography.Title level={5}>
                              Attendance
                            </Typography.Title>
                          </Col>
                        </Row>
                      </Col>
                      <Col xs={12} sm={12} md={12} lg={12} xl={12} xxl={12}>
                        <Row gutter={10} justify='end'>
                          <Col style={{ textAlign: 'end' }}>
                            <Space>
                              <InputNumber
                                defaultValue={0}
                                value={attendanceValue}
                                min={0}
                                max={100}
                                formatter={(value) => `${value}%`}
                                parser={(value) => value.replace('%', '')}
                                onChange={(e) => setAttendanceValue(e)} />
                              <Button type='primary'
                                onClick={() => {
                                  setAttendance()
                                }}
                                loading={isSavingAttendance}
                                disabled={isSavingAttendance}
                              >Save</Button>
                            </Space>
                          </Col>
                        </Row>
                      </Col>
                    </Row>
                  </div>
                  <Row>
                    <Col span={24}>
                      <Collapse
                        accordion
                        bordered={false}
                        expandIconPosition='end'
                        style={{
                          backgroundColor: 'transparent'
                        }}
                      >
                        {
                          courses && courses.map(c => renderCourse({ ...c, infTipo: { catValor: 'PLAN_TRABAJO' } }))
                        }
                      </Collapse>
                    </Col>
                  </Row>
                  <Row>
                    <Col span={24}>
                      <Collapse
                        accordion
                        bordered={false}
                        expandIconPosition='end'
                        style={{
                          backgroundColor: 'transparent'
                        }}
                      >
                        {
                          courses && courses.map(c => renderCourse({ ...c, infTipo: { catValor: 'INFORME_FINAL' } }))
                        }
                      </Collapse>
                    </Col>
                  </Row>
                  {
                    postulant && postulant.informeList && postulant.certificadoList &&
                    <>
                      {
                        postulant.informeList.filter(w => w.infTipo.catValor === 'INFORME_FINAL' &&
                          w.infEstado.catValor === 'EST_INF_APR'
                        ).length > 0
                        &&
                        <>
                          {
                            postulant.certificadoList && postulant.certificadoList.map(c => renderCertificate(c))
                          }
                        </>
                      }
                    </>
                  }
                </>
              }]
            }>
            </Tabs >
          </>
      }
    </>
  )
}

export default Applicant