import React, { useState, useEffect } from 'react'
import { Row, Col, Image, Form, Input, Select, DatePicker, Switch, Button, Typography, Tooltip, Space, Spin, message } from 'antd'
import * as anticons from '@ant-design/icons'
import toast from 'react-hot-toast'

import { getCatalogos } from './../../utils/Utils'

const { Option } = Select

const User = (props) => {

  const { user, navigate } = props
  const { id } = user
  const [formUser] = Form.useForm()

  const [load, setLoad] = useState(true)
  const [isLoading, setIsLoading] = useState(false)
  const [isSaving, setIsSaving] = useState(false)
  const [error, setError] = useState(null)
  const [currentlyWorking, setCurrentlyWorking] = useState(true)

  const renderPaisValor = e => <Option key={e.catValor} value={e.catId}>
    <>
      <img
        src={`https://flagcdn.com/24x18/${e.catDescripcion.toLowerCase()}.png`}
        srcSet={`https://flagcdn.com/48x36/${e.catDescripcion}.png 2x,https://flagcdn.com/72x54/${e.catDescripcion}.png 3x`}
        width="24"
        height="18"
        alt={e.catNombre}></img>
      &nbsp;{
        e.catNombre
      }
    </>
  </Option>

  const renderPaisCode = e => <Option key={e.catValor} value={e.catId}>
    <>
      +({e.catValor})&nbsp;
      <img
        src={`https://flagcdn.com/24x18/${e.catDescripcion.toLowerCase()}.png`}
        srcSet={`https://flagcdn.com/48x36/${e.catDescripcion}.png 2x,https://flagcdn.com/72x54/${e.catDescripcion}.png 3x`}
        width="24"
        height="18"
        alt={e.catDescripcion}></img>
    </>
  </Option>

  const update = async (values) => {
    message.info('Saved')
  }

  return (
    <>
      <div className="grid">
        <div className="col-12">
          <div className="mgeCard">
            <div className="grid">
              <div className="col-4">
                <h3 style={{ padding: '0', margin: '0' }} className='mge-darkGreen'>
                  My Account
                </h3>
              </div>
              <div className="col-8" style={{ textAlign: 'right' }}>
                <Space>
                  <Button
                    type='default'
                    onClick={() => {
                      window.history.go(-1)
                    }}
                  >
                    Cancel
                  </Button>
                  <Button
                    type='primary'
                    disabled={isLoading}
                    onClick={() => { formUser.submit() }}>Save</Button>
                </Space>
              </div>
            </div>
          </div>
        </div>
      </div>
      {
        isLoading ?
          <Row style={{ padding: '30px 10px' }}>
            <Col span={24} style={{ textAlign: 'center' }}>
              <Spin size='large' tip={
                <Typography.Paragraph style={{ padding: '10px 0' }}>
                  Loading...
                </Typography.Paragraph>
              } />
            </Col>
          </Row>
          :
          user && <Row justify='center' style={{ paddingTop: '20px' }}>
            <Col xs={24} sm={24} md={22} lg={20} xl={18} xxl={16}>
              <Form
                form={formUser}
                layout={'vertical'}
                initialValues={user}
                onFinish={update}
                disabled={isSaving}
              >
                <Row gutter={[10, 0]}>
                  <Col xs={24} sm={8} md={8} lg={8} xl={8} xxl={8}>
                    <Row>
                      <Col span={24}>
                        <Typography.Paragraph style={{ textAlign: 'center' }}>
                          Upload your profile picture
                        </Typography.Paragraph>
                      </Col>
                      <Col span={24}>
                        <Typography.Paragraph style={{ textAlign: 'center' }}>
                          <Image src='./../assets/user.png' preview={false} />
                        </Typography.Paragraph>
                      </Col>
                      <Col span={24} style={{ textAlign: 'center' }}>
                        <Form.Item>
                          <Button type='primary'>Upload photo</Button>
                        </Form.Item>
                      </Col>
                    </Row>
                  </Col>
                  <Col xs={24} sm={16} md={16} lg={16} xl={16} xxl={16}>
                    <Row gutter={[15, 0]}>
                      <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                        <Form.Item
                          name='nombres'
                          label="First Name"
                          rules={[
                            {
                              required: true,
                              message: 'Complete first Name',
                            },
                          ]}
                        >
                          <Input />
                        </Form.Item>
                      </Col>
                      <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                        <Form.Item
                          name='apellidos'
                          label="Last Name"
                          rules={[
                            {
                              required: true,
                              message: 'Complete last Name',
                            },
                          ]}
                        >
                          <Input />
                        </Form.Item>
                      </Col>
                      <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                        <Form.Item
                          name={['sexoId']}
                          label='Sex'
                          rules={[
                            {
                              required: true,
                              message: 'Select sex',
                            },
                          ]}
                        >
                          <Select
                            style={{
                              width: '100%',
                              display: 'inline-block'
                            }}
                            fieldNames={{
                              label: 'catNombre',
                              value: 'catId'
                            }}
                            showSearch
                            options={getCatalogos().generos}
                            optionFilterProp="catNombre"
                          />
                        </Form.Item>
                      </Col>
                      <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                        <Form.Item
                          name='fechanac'
                          label="Date of birth"
                          rules={[
                            {
                              required: true,
                              message: 'Select date of birth',
                            },
                          ]}
                        >
                          <DatePicker format={'YYYY-MM-DD'} style={{ width: '100%' }} />
                        </Form.Item>
                      </Col>
                    </Row>
                  </Col>
                  <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <Form.Item
                      name='documento'
                      label={
                        <Space direction='horizontal'>
                          <>ID Number</>
                          <Tooltip
                            title={
                              <>
                                This is the unique identification number issued by the country of residence.
                              </>
                            }
                            placement='right'
                          >
                            <anticons.InfoCircleFilled />
                          </Tooltip>
                        </Space>
                      }
                      rules={[
                        {
                          required: true,
                          message: 'Complete ID Number',
                        },
                      ]}
                    >
                      <Input />
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <Form.Item
                      name='paisId'
                      label="Country"
                      rules={[
                        {
                          required: true,
                          message: 'Select country',
                        },
                      ]}
                    >
                      <Select name='paisId'>
                        {
                          getCatalogos().paises.map(p => renderPaisValor(p))
                        }
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <Form.Item
                      name='ciudad'
                      label="City"
                      rules={[
                        {
                          required: true,
                          message: 'Complete city',
                        },
                      ]}
                    >
                      <Input />
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <Form.Item
                      name='codigotel'
                      label="Country Code"
                      rules={[
                        {
                          required: true,
                          message: 'Select country code',
                        }
                      ]}
                    >
                      <Select name='codigotel'>
                        {
                          getCatalogos().paises.map(p => renderPaisCode(p))
                        }
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <Form.Item
                      name='celular'
                      label="Phone Number"
                      rules={[
                        {
                          required: true,
                          message: 'Complete phone number',
                        },
                        {
                          type: 'number',
                          message: 'Phone number is not valid'
                        }
                      ]}
                    >
                      <Input />
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={8} lg={8} xl={8} xxl={8}>
                    <Form.Item
                      name='lugartrabajo'
                      label={
                        <Space>
                          Are you currently working?
                          <Switch defaultChecked onChange={(c) => { setCurrentlyWorking(c) }} />
                        </Space>
                      }
                      rules={[
                        {
                          required: currentlyWorking,
                          message: 'Are you currently working?',
                        },
                      ]}
                    >
                      <Input name='lugartrabajo' disabled={!currentlyWorking} />
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                    <Form.Item
                      name='email'
                      label="Email"
                      rules={[
                        {
                          required: true,
                          message: 'Complete email',
                        },
                        {
                          type: 'email',
                          message: 'Email is not valid'
                        }
                      ]}
                    >
                      <Input disabled />
                    </Form.Item>
                  </Col>
                  <Col xs={24} sm={12} md={12} lg={12} xl={12} xxl={12}>
                    <Form.Item
                      name='email2'
                      label="Additional Email"
                      rules={[
                        {
                          required: true,
                          message: 'Complete additional email',
                        },
                        {
                          type: 'email',
                          message: 'Additional email is not valid'
                        }
                      ]}
                    >
                      <Input />
                    </Form.Item>
                  </Col>
                  {/* <Col span={24} style={{ textAlign: 'center' }}>
                    <Form.Item>
                      <Button type='primary' htmlType='submit' loading={isSaving} disabled={isSaving} block>Save</Button>
                    </Form.Item>
                  </Col> */}
                </Row>
              </Form>
            </Col>
          </Row>
      }
    </>
  )
}

export default User